    </div>
</template>
<script>
    export default {
        data(){
            return{
                popupActive: false,
                index: 0,
            }
        },
        methods:{
            changeSlide : function (input) {
                if (((this.index + input) > -1) && ((this.index + input) < 4)) {
                    this.index = this.index + input;
                }
            },
            savePosition : function () {
                $('html').css({'overflow':'auto'});
                localStorage.pagePosition = 3;
                this.popupActive = true;
            }
        }
    }
</script>
